@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2"), url("../fonts/OpenSans-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2"), url("../fonts/OpenSans-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2"), url("../fonts/OpenSans-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Bold.woff2") format("woff2"), url("../fonts/OpenSans-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rouble";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/rouble.woff2") format("woff2"), url("../fonts/rouble.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
}

html {
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.block-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1439px) {
  .block-wrapper {
    display: flex;
    flex-direction: column;
  }
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 135px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 35px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 21px;
  font-weight: 600;
  display: inline-flex;
  padding: 16px 48px 14px;
  border-radius: 6px;
  color: #000000;
  background-color: #0ad9c6;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}
.btn:active:not(.focus-visible) {
  color: #ffffff;
  opacity: 0.3;
}
.btn.focus-visible:active {
  color: #ffffff;
  opacity: 0.3;
}

.input {
  position: relative;
  display: flex;
}
.input label {
  display: flex;
  align-items: center;
  width: 100%;
}
@media (max-width: 767px) {
  .input label {
    align-items: flex-start;
    flex-direction: column;
  }
}
.input label input {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  padding-left: 25px;
  color: #1c1c1c;
  flex-grow: 1;
  min-height: 48px;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  transition: border 0.3s ease;
}
@media (max-width: 767px) {
  .input label input {
    max-width: unset;
    width: 100%;
  }
}
.input label input::placeholder {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: rgba(28, 28, 28, 0.5);
}
.input label input:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.input label input:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
@media (max-width: 767px) {
  .input__label {
    margin: 0 0 18px 0;
  }
}
.input__error {
  position: absolute;
  right: 0;
  bottom: 35px;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.3s ease;
}
@media (max-width: 767px) {
  .input__error {
    bottom: 20px;
  }
}
.input.is-invalid input {
  border: 5px solid #ff1553;
}
.input.is-invalid input:focus, .input.is-invalid input:hover {
  border: 5px solid rgba(255, 21, 83, 0.5);
}
.input.is-valid input {
  border: 2px solid #22b259;
}
.input.is-valid input:focus, .input.is-valid input:hover {
  border: 2px solid rgba(34, 178, 89, 0.5);
}
.input .input-message {
  position: absolute;
  right: 0;
  bottom: 0;
}
.input .input-message.is-invalid {
  color: #ff0000;
}
.input .input-message.is-valid {
  color: #15ab07;
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 30;
}

.header .no-js {
  position: relative;
}

.site-nav {
  display: flex;
  position: relative;
  top: 0;
  max-width: 875px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1439px) {
  .site-nav {
    width: 100%;
    max-width: unset;
  }
}

.site-nav__logo {
  position: absolute;
  top: 320px;
  left: 0;
}
@media (max-width: 1439px) {
  .site-nav__logo {
    top: 25px;
    left: 20px;
  }
}
@media (max-width: 767px) {
  .site-nav__logo {
    left: 15px;
  }
}

.site-nav__list {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  flex-wrap: wrap;
  width: 100%;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
@media (max-width: 1439px) {
  .site-nav__list--closed {
    display: none;
  }
}
@media (max-width: 1439px) {
  .site-nav__list {
    flex-direction: column;
    border-bottom: none;
    justify-content: flex-start;
    gap: 30px;
    z-index: 10;
    background-color: #f6f7f9;
    padding-top: 100px;
    padding-bottom: 100px;
    height: 100vh;
    width: 320px;
    margin-left: auto;
  }
}

.site-nav__item {
  display: flex;
  align-items: center;
  list-style-type: none;
}
@media (max-width: 1439px) {
  .site-nav__item {
    background-color: #f6f7f9;
  }
}

.site-nav__link {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 32px;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7);
  text-decoration: none;
  padding: 21px 20px 9px;
  display: block;
}
@media (max-width: 1439px) {
  .site-nav__link {
    font-family: "OpenSans", "Arial", sans-serif;
    font-size: 20px;
    line-height: 28px;
    font-weight: 400;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #48494d;
    width: 100%;
    padding: 16px;
  }
}
.site-nav__link:hover {
  background-color: #ffffff;
  color: #1f1f1f;
}
@media (max-width: 1439px) {
  .site-nav__link:hover {
    background-color: #1f1f1f;
    color: #ffffff;
  }
}
.site-nav__link:hover .site-nav__arrow {
  color: #ffffff;
}
.site-nav__link:focus {
  background-color: #ffffff;
  color: #1f1f1f;
  outline: none;
}
@media (max-width: 1439px) {
  .site-nav__link:focus {
    background-color: #1f1f1f;
    color: #ffffff;
  }
}
.site-nav__link:focus .site-nav__arrow {
  color: #ffffff;
}
.site-nav__link:active {
  background-color: rgba(255, 255, 255, 0.3);
  color: #ffffff;
}
@media (max-width: 1439px) {
  .site-nav__link:active {
    background-color: #1f1f1f;
    color: rgba(255, 255, 255, 0.7);
  }
}
.site-nav__link:active .site-nav__arrow {
  color: rgba(255, 255, 255, 0.7);
}

.site-nav__arrow {
  display: none;
}
@media (max-width: 1439px) {
  .site-nav__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgba(44, 46, 63, 0.5);
  }
}

.site-nav__toggle {
  display: none;
  border: none;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  z-index: 20;
  background-color: #0ad9c6;
  background-image: url("../img/burger.svg");
  transition: opacity 0.3s ease;
}
.site-nav__toggle--close {
  background-color: transparent;
  background-image: url("../img/cross.svg");
}
@media (hover: hover) {
  .site-nav__toggle:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.site-nav__toggle.focus-visible:focus {
  opacity: 0.8;
}
.site-nav__toggle:active:not(.focus-visible) {
  opacity: 0.3;
}
.site-nav__toggle.focus-visible:active {
  opacity: 0.3;
}
@media (max-width: 1439px) {
  .site-nav__toggle {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    width: 50px;
    height: 50px;
  }
}

.no-js .site-nav {
  position: relative;
  flex-direction: column;
  align-items: center;
  padding-top: 25px;
  background-color: #131314;
}
.no-js .site-nav__logo {
  position: relative;
  top: 0;
}
.no-js .site-nav__toggle {
  display: none;
}
.no-js .site-nav__list {
  width: 100%;
  height: unset;
  z-index: 0;
  background-color: transparent;
  padding-top: 25px;
  padding-bottom: 25px;
}
.no-js .site-nav__item {
  background-color: transparent;
}
.no-js .site-nav__link {
  color: rgba(255, 255, 255, 0.7);
}
.no-js .site-nav__link:hover {
  color: #ffffff;
}
.no-js .site-nav__link:focus {
  color: #ffffff;
}
.no-js .site-nav__link:active {
  opacity: 0.3;
}
.no-js .site-nav__arrow {
  color: rgba(255, 255, 255, 0.7);
}
.no-js .site-nav__arrow:active {
  opacity: 0.3;
}

.hero {
  background-color: #0e0e0e;
  background-image: url("../img/common/bike-desktop.jpg");
  background-repeat: no-repeat;
  background-position-x: 50%;
  min-height: 825px;
}
@media (max-width: 1439px) {
  .hero {
    background-image: url("../img/common/bike-tablet.jpg");
    min-height: 340px;
    background-position-y: -100px;
  }
}
@media (max-width: 767px) {
  .hero {
    background-image: url("../img/common/bike-mobile.jpg");
    min-height: 390px;
    background-position-y: 0;
  }
}

.hero__wrapper {
  display: flex;
  max-width: 875px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 320px;
  gap: 150px;
}
@media (max-width: 1439px) {
  .hero__wrapper {
    max-width: unset;
    flex-direction: column;
    padding-top: 0;
    gap: 0;
  }
}

.hero__description {
  padding-top: 50px;
}
@media (max-width: 1439px) {
  .hero__description {
    max-width: 435px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 135px;
    padding-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .hero__description {
    padding: 120px 15px 70px;
  }
}

.hero__description h1 {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 46px;
  line-height: 64px;
  font-weight: 700;
  letter-spacing: -0.23px;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 25px;
}
@media (max-width: 1439px) {
  .hero__description h1 {
    font-family: "OpenSans", "Arial", sans-serif;
    font-size: 30px;
    line-height: 42px;
    font-weight: 700;
    letter-spacing: -0.15px;
    text-align: center;
    margin-bottom: 20px;
  }
}

.hero__description p {
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
  letter-spacing: -0.1px;
  color: rgba(255, 255, 255, 0.8);
  margin-top: 0;
  margin-bottom: 0;
}
@media (max-width: 1439px) {
  .hero__description p {
    font-family: "OpenSans", "Arial", sans-serif;
    font-size: 18px;
    line-height: 32px;
    font-weight: 300;
    letter-spacing: -0.09px;
    text-align: center;
  }
}

@media (max-width: 1439px) {
  .hero__form {
    background-color: #ffffff;
    padding-top: 70px;
    padding-bottom: 35px;
  }
}

@media (max-width: 1439px) {
  .hero__form form {
    max-width: 325px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .hero__form form {
